"use client"

import React, { useRef, useState, useEffect } from 'react';
import Image from 'next/image';
import Link from 'next/link';
import { Plus } from 'lucide-react';
import { Carousel, CarouselContent, CarouselItem, type CarouselApi } from "@/components/ui/carousel"
import { PlaceHolderImages } from '@/lib/placeholder-images';
import Autoplay from "embla-carousel-autoplay"

type Cake = {
    name: string;
    description: string;
    weight: string;
    productionTime: string;
    price: string;
    imageId: string;
}

const carouselCakes: Cake[] = [
    {
        name: 'Vintage',
        description: 'Sponge cake with cocoa in combination with cappuccino cream and blueberries.',
        weight: '2.4 KG',
        productionTime: '72 H',
        price: '$188,90',
        imageId: 'hero-1'
    },
    {
        name: 'Chocolate Dream',
        description: 'Rich dark chocolate cake with a molten lava center.',
        weight: '1.8 KG',
        productionTime: '48 H',
        price: '$150,00',
        imageId: 'hero-2'
    },
    {
        name: 'Rainbow Delight',
        description: 'A vibrant six-layered rainbow cake with vanilla frosting.',
        weight: '2.8 KG',
        productionTime: '96 H',
        price: '$250,00',
        imageId: 'hero-3'
    },
    {
        name: 'Sweet Swirl',
        description: 'A classic vanilla cupcake with a beautiful rose frosting swirl.',
        weight: '0.2 KG',
        productionTime: '24 H',
        price: '$25,00',
        imageId: 'hero-4'
    }
];

export default function HeroCarousel() {
  const [api, setApi] = useState<CarouselApi>()
  const [current, setCurrent] = useState(0)
  const [currentCake, setCurrentCake] = useState(carouselCakes[0]);

  const plugin = useRef(
    Autoplay({ delay: 4000, stopOnInteraction: true })
  )

  useEffect(() => {
    if (!api) {
      return
    }

    setCurrent(api.selectedScrollSnap())

    const onSelect = () => {
      const selectedIndex = api.selectedScrollSnap();
      setCurrent(selectedIndex);
      setCurrentCake(carouselCakes[selectedIndex]);
    };

    api.on("select", onSelect)
    
    return () => {
      api.off("select", onSelect)
    }
  }, [api])

  const heroImages = carouselCakes.map(cake => {
      const placeholder = PlaceHolderImages.find((img) => img.id === cake.imageId)
      return {
          ...cake,
          imageUrl: placeholder?.imageUrl,
          imageHint: placeholder?.imageHint,
          alt: placeholder?.description
      }
  })

  return (
    <div className="relative flex justify-center items-center">
        <Carousel 
            setApi={setApi}
            plugins={[plugin.current]}
            className="w-full max-w-md"
            onMouseEnter={plugin.current.stop}
            onMouseLeave={plugin.current.reset}
        >
            <CarouselContent>
                {heroImages.map((img, index) => (
                    <CarouselItem key={index}>
                        <div className="relative w-full aspect-square">
                            {img.imageUrl && (
                                <Image
                                    src={img.imageUrl}
                                    alt={img.alt || ''}
                                    fill
                                    className="object-contain"
                                    priority={index === 0}
                                    data-ai-hint={img.imageHint}
                                />
                            )}
                        </div>
                    </CarouselItem>
                ))}
            </CarouselContent>

            <div className="absolute top-1/2 -right-10 transform -translate-y-1/2 bg-white/10 backdrop-blur-sm p-3 rounded-lg flex items-center gap-2 text-white">
                <span>Cake "{currentCake.name}"</span>
                <Link href="/custom-order">
                  <button className="bg-white/20 rounded-full p-1"><Plus className="w-4 h-4" /></button>
                </Link>
            </div>
        </Carousel>

        <div className="hidden lg:flex flex-col gap-6 absolute -right-40 top-1/2 transform -translate-y-1/2 text-white text-sm w-48">
            <div>
                <p className="text-white/60">Cake description</p>
                <p>{currentCake.description}</p>
            </div>
            <div>
                <p className="text-white/60">Cake weight</p>
                <p>{currentCake.weight}</p>
            </div>
            <div>
                <p className="text-white/60">Production time</p>
                <p>{currentCake.productionTime}</p>
            </div>
            <div>
                <p className="text-white/60">Product price</p>
                <p className="text-2xl font-bold">{currentCake.price}</p>
            </div>
        </div>

        <div className="flex justify-center absolute -bottom-12">
            <div className="flex gap-2">
                {carouselCakes.map((_, i) => (
                    <button key={i} onClick={() => api?.scrollTo(i)} className={`w-2 h-2 rounded-full ${i === current ? 'bg-white' : 'bg-white/50'}`}></button>
                ))}
            </div>
        </div>
    </div>
  )
}
